import {useGetPostQuery} from "../../utils/getApi";
import Loading from "../Loading/Loading";
import {Parser} from "html-to-react";
import reformat from "../../utils/reformat";
import React from "react";

function Comment({id, isFetching}) {

    const {data, isLoading, refetch} = useGetPostQuery(id);
    if (isFetching) {refetch()}
    if (isLoading) return <Loading/>;

    const htmlToReactParser = new Parser();
    return data && (
        <ul className="comment-container">
            <h3 className="comment">{data.by} : {reformat(data.time)}</h3>
            <span className="comment-text">{htmlToReactParser.parse(data.text)}</span>
            {data.kids &&
            <ul>
                {data.kids.map(commentId => <Comment key={commentId} id={commentId} isFetching={isFetching}/>)}
            </ul>}
        </ul>
    )
}

export default Comment;

