import React from 'react';
import {useEffect, useState} from "react";
import {useGetPostsQuery} from "../../utils/getApi";
import Posts from "./Posts";


function MainPage() {
    //      
    const [updatePost, setUpdatePost] = useState(false);
    useEffect(() => {
        setTimeout(() => {
            setUpdatePost(!updatePost);
        }, 60000)
    }, [updatePost]);
    const {refetch} = useGetPostsQuery();
    useEffect(() => {
        refetch()
    }, [updatePost, refetch]);


    //       
    const [scrollPosition, setScrollPosition] = useState({
        scrollX: 0,
        scrollY: 0,
    });

    useEffect(() => {
        setScrollPosition((prevScrollCord) => {
            return {
                ...prevScrollCord,
                ...JSON.parse(window.localStorage.getItem("scrollCord")),
            };
        });
        setTimeout(() => {
            window.scrollTo({
                top: scrollPosition.scrollY,
                left: 0,
                behavior: 'smooth',
            });
        }, 3500);

    }, [scrollPosition.scrollX, scrollPosition.scrollY]);

    function onScroll() {
        let cordsAfterClick = {};
        let cords = ["scrollX", "scrollY"];
        cords.forEach((cord) => {
            cordsAfterClick[cord] = window[cord];
        });
        localStorage.setItem("scrollCord", JSON.stringify(cordsAfterClick));
    }

    //  ,   
    const [showTopBtn, setShowTopBtn] = useState(false);
    useEffect(() => {
        window.addEventListener('scroll', () => {
            if (window.scrollY > 300) {
                setShowTopBtn(true);
            } else {
                setShowTopBtn(false);
            }
        });
    }, []);
    const goToTop = () => {
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth',
        });
    };

    return (
        <div className="main-container">
        <div className="main-header">
            <h2 className='main-head'>This is all news.. refresh?</h2>
            <button className="refresh-button" onClick={() => refetch()}>
                <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 459 459">
                    <g>
                        <g>
                            <path d="M229.5,0C102.751,0,0,102.751,0,229.5S102.751,459,229.5,459S459,356.249,459,229.5S356.249,0,229.5,0z M376.938,266.841
			c-3.704,5.5-9.44,9.303-15.948,10.572c-1.589,0.31-3.192,0.463-4.788,0.463c-4.94,0-9.808-1.464-13.965-4.264l-46.315-31.192
			c-11.452-7.712-14.483-23.249-6.771-34.701c7.255-10.771,21.426-14.09,32.614-8.032c-12.472-36.96-47.469-63.647-88.583-63.647
			c-51.534,0-93.46,41.926-93.46,93.46s41.926,93.46,93.46,93.46c13.807,0,25,11.193,25,25s-11.193,25-25,25
			c-79.104,0-143.46-64.355-143.46-143.46s64.355-143.46,143.46-143.46c64.713,0,119.547,43.075,137.359,102.056
			c8.322-7.607,21.052-8.848,30.818-2.271c11.452,7.712,14.483,23.249,6.772,34.701L376.938,266.841z"/>
                        </g>
                    </g>
                </svg>
            </button>
        </div>
        <Posts onScroll={onScroll}/>
        {showTopBtn && (
           <button onClick={goToTop} className="main-up-btn">
               <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 300.003 300.003"><g><g><path d="M150,0C67.159,0,0.001,67.159,0.001,150c0,82.838,67.157,150.003,149.997,150.003S300.002,232.838,300.002,150
			C300.002,67.159,232.842,0,150,0z M217.685,189.794c-5.47,5.467-14.338,5.47-19.81,0l-48.26-48.27l-48.522,48.516
			c-5.467,5.467-14.338,5.47-19.81,0c-2.731-2.739-4.098-6.321-4.098-9.905s1.367-7.166,4.103-9.897l56.292-56.297
			c0.539-0.838,1.157-1.637,1.888-2.368c2.796-2.796,6.476-4.142,10.146-4.077c3.662-0.062,7.348,1.281,10.141,4.08
			c0.734,0.729,1.349,1.528,1.886,2.365l56.043,56.043C223.152,175.454,223.156,184.322,217.685,189.794z"/>
               </g></g></svg></button>
        )}
    </div>
    );
}

export default MainPage;
