import React from 'react';
import Loading from "../Loading/Loading";
import Post from "../Post/Post";
import {useGetPostsQuery} from "../../utils/getApi";

function Posts ({ onScroll}) {
    const { data: posts, isLoading, isSuccess, isError, isFetching } = useGetPostsQuery();

    // 
    let content;
    if (isLoading) {
        content = <Loading/>
    } else if (isFetching) {
        content = <p className='posts-zero'>Loading..</p>
    }
    else if (isSuccess) {
        content = posts.map(id => {
            return <Post onScroll={onScroll} key={id} id={id} isFetching={isFetching} />;
        });
    } else if (isError) {
        content = <p className='posts-zero'>No posts, sorry :(</p>
    }

    //   
    if (!posts || posts.length === 0) return <p className='posts-zero'>Loading..</p>;


    return (
        <div className="posts">
            <ul> {content} </ul>
        </div>
    )
}

export default Posts;
