import React, {memo, useEffect, useState} from 'react';
import {useGetPostQuery} from "../../utils/getApi";
import {useHistory, useParams} from "react-router";
import reformat from "../../utils/reformat";
import RootComment from "../Comments/RootComment";


function PostPage() {
    let {id} = useParams();
    const { data:post, isLoading, refetch, isFetching } = useGetPostQuery(id);
    const history = useHistory();

    const [updateBtn, setUpdateBtn] = useState(false);
    useEffect(() => {
        if (isFetching) {
            setUpdateBtn(true);
        } else {
            setUpdateBtn(false);
        }
    }, [isFetching]);

    if (isLoading) return <p className='posts-zero'>Loading..</p>

    // if (isFetching) return <p className='posts-zero'>Loading..</p>

    function goBack() {
        history.goBack();
    }

    return (
        <div className="postPage">
            <div className="postPage-buttons">
                back
                <button onClick={goBack} className="postPage-buttons-back" aria-label="back"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 300.003 300.003">
                    <g>
                        <g>
                            <path d="M150,0C67.159,0,0.001,67.159,0.001,150c0,82.838,67.157,150.003,149.997,150.003S300.002,232.838,300.002,150
			C300.002,67.159,232.839,0,150,0z M189.226,218.202c-2.736,2.734-6.321,4.101-9.902,4.101c-3.582,0-7.169-1.367-9.902-4.103
			l-56.295-56.292c-0.838-0.537-1.639-1.154-2.368-1.886c-2.796-2.799-4.145-6.479-4.077-10.144
			c-0.065-3.667,1.281-7.35,4.077-10.146c0.734-0.731,1.53-1.349,2.368-1.886l56.043-56.043c5.47-5.465,14.34-5.467,19.808,0.003
			c5.47,5.467,5.47,14.335,0,19.808l-48.265,48.265l48.514,48.516C194.695,203.864,194.695,212.732,189.226,218.202z"/>
                        </g>
                    </g>
                </svg></button>

                { !updateBtn && (
                <button aria-label="update comments" className="postPage-buttons-refresh" onClick={() => refetch()}>
                        <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 459 459">
                            <g>
                                <g>
                                    <path d="M229.5,0C102.751,0,0,102.751,0,229.5S102.751,459,229.5,459S459,356.249,459,229.5S356.249,0,229.5,0z M376.938,266.841
			c-3.704,5.5-9.44,9.303-15.948,10.572c-1.589,0.31-3.192,0.463-4.788,0.463c-4.94,0-9.808-1.464-13.965-4.264l-46.315-31.192
			c-11.452-7.712-14.483-23.249-6.771-34.701c7.255-10.771,21.426-14.09,32.614-8.032c-12.472-36.96-47.469-63.647-88.583-63.647
			c-51.534,0-93.46,41.926-93.46,93.46s41.926,93.46,93.46,93.46c13.807,0,25,11.193,25,25s-11.193,25-25,25
			c-79.104,0-143.46-64.355-143.46-143.46s64.355-143.46,143.46-143.46c64.713,0,119.547,43.075,137.359,102.056
			c8.322-7.607,21.052-8.848,30.818-2.271c11.452,7.712,14.483,23.249,6.772,34.701L376.938,266.841z"/>
                                </g>
                            </g>
                        </svg>
                    </button> )}
                { updateBtn && (
                <button aria-label="update comments" className="postPage-buttons-refresh-close" >
                    <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 459 459">
                        <g>
                            <g>
                                <path d="M229.5,0C102.751,0,0,102.751,0,229.5S102.751,459,229.5,459S459,356.249,459,229.5S356.249,0,229.5,0z M376.938,266.841
			c-3.704,5.5-9.44,9.303-15.948,10.572c-1.589,0.31-3.192,0.463-4.788,0.463c-4.94,0-9.808-1.464-13.965-4.264l-46.315-31.192
			c-11.452-7.712-14.483-23.249-6.771-34.701c7.255-10.771,21.426-14.09,32.614-8.032c-12.472-36.96-47.469-63.647-88.583-63.647
			c-51.534,0-93.46,41.926-93.46,93.46s41.926,93.46,93.46,93.46c13.807,0,25,11.193,25,25s-11.193,25-25,25
			c-79.104,0-143.46-64.355-143.46-143.46s64.355-143.46,143.46-143.46c64.713,0,119.547,43.075,137.359,102.056
			c8.322-7.607,21.052-8.848,30.818-2.271c11.452,7.712,14.483,23.249,6.772,34.701L376.938,266.841z"/>
                            </g>
                        </g>
                    </svg>
                </button>
                    )}
                update
            </div>

            <h3 className="postPage-title">{post.title}</h3>
            <div className="postPage-container">
                 <div className="postPage-info">
                     Post by {post.by} | {reformat(post.time)} | Comments: {post.descendants}
                 </div>
                {post.url && <a className="postPage-link" href={post.url}>Read post here</a>}
                {post.kids &&
                <div className="postPage-comments">
                    Comments:
                    <ul> {post.kids.map((commentId) => <RootComment key={commentId} id={commentId}
                                                                    isFetching={isFetching}/>)} </ul>
                </div>
                }
            </div>
        </div>
    )
}

export default memo(PostPage);
