import React from 'react'
import {useState} from "react";
import {useGetPostQuery} from "../../utils/getApi";
import reformat from "../../utils/reformat";
import Comment from "./Comment";
import Loading from "../Loading/Loading";
import {Parser} from "html-to-react";
import PropTypes from "prop-types";

function RootComment({id, isFetching}) {
    const [isOpen, setIsOpen] = useState(false);

    const {data, isLoading, refetch } = useGetPostQuery(id);
    if (isFetching) {refetch()}

    const handleClick = function() {
        setIsOpen(!isOpen)
    };

    const htmlToReactParser = new Parser();
    if (isLoading) return <Loading/>
    return data && (
        <div className="rootComments">
            <h3 className="rootComments-date">{data.by} : {reformat(data.time)}</h3>
            <div>
            <span className="rootComments-text">{htmlToReactParser.parse(data.text)}</span></div>
            {data.kids &&
            <div className="rootComments-btn" onClick={handleClick}>{isOpen ? 'Hide' : 'Show replies'}</div> }
            {isOpen && <ul>
                {data.kids.map(commentId => <Comment key={commentId} id={commentId} isFetching={isFetching}/>)}</ul>}
        </div>
    )
}

RootComment.propTypes = {
    id: PropTypes.number.isRequired,
    isFetching: PropTypes.bool.isRequired
}

export default RootComment;
